
































































































































import Vue from 'vue';
import { mapState } from "vuex";
import menuJSON from '../../menu.json'

export default Vue.extend({
    name: 'RomaHeader',
    props: {
        navigationMenu: {
          type: Boolean,
          default: true,
        }
    },
    data () {
        return {
            school: this.$store.state.selectedSchool,
            yearSelected: new Date().getFullYear().toString(),
            openedMenu: false,
            navigation: menuJSON,            }
    },
    computed: {
        menu () {
            return [
                { title: this.$vuetify.lang.t('$vuetify.generals.profile'), route: {}},
                { title: this.$vuetify.lang.t('$vuetify.generals.changePassword'), route: {}},
                { title: this.$vuetify.lang.t('$vuetify.generals.help'), route: {}},
                { title: this.$vuetify.lang.t('$vuetify.generals.closeSession'), route: {}},
            ]
        },
        ...mapState([
            'schools',
            'user'
        ])
    },
    methods: {
      selectSchool () {
          this.$store.commit('selectSchool', this.school);
          this.yearSelected = new Date().getFullYear().toString();
      },
    },
});
