import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

// Plugins Vuex
import VuejsStorage from 'vuejs-storage';

// Default language by vuetify
import Vuetify from '../src/plugins/vuetify';

Vue.use(Vuex);

// Configuración básicas axios
const AXIOS_DEFAULT_CONFIG = {
    baseURL: 'https://reqres.in/api',
    timeout: 15000, // Si nunca responde el servidor
    responseType: 'json',
};

export default new Vuex.Store({
  state: {
    selectedLanguage: Vuetify.preset.lang.current,
    selectedSchool: {id: 2000, name: 'Roma Demo', logo: 'logo.svg', years: ['2018', '2019']},
    user: null,
    config: {},
    languages: [
      //Iconos tomados de: https://www.flaticon.com/packs/countrys-flags
      {title: 'Español', i18n: 'es', icon: 'https://image.flaticon.com/icons/png/512/197/197593.png'},
      {title: 'Ingles', i18n: 'en', icon: 'https://image.flaticon.com/icons/png/128/197/197484.png'},
    ],
    schools: [
      {id: 2000, name: 'Roma Demo', logo: 'logo.svg', years: ['2018', '2019']},
      {id: 4856, name: 'Ejemplo 2', logo: 'logo.svg', years: ['2019']},
      {id: 5178, name: 'Ejemplo 3', logo: 'logo.svg', years: ['2017', '2018', '2019']},
      {id: 9874, name: 'Ejemplo 4', logo: 'logo.svg', years: ['2016', '2017', '2018', '2019']},
      {id: 1594, name: 'Ejemplo 5', logo: 'logo.svg', years: ['2018', '2019']},
    ],
    info: {
      contact: {
        phone: '+569 3458 0660',
        sac: '229293270',
        email: 'sac@romacl.com',
        location: {lat: '10.2099343', lon: '-68.0119684,12z'},
      },
      social: [
        {icon: 'instagram', url: 'http://instagram.com/'},
        {icon: 'facebook', url: 'http://facebook.com/'},
        {icon: 'youtube', url: 'http://youtube.com/'},
        // {icon: 'twitter', url: 'http://twitter.com/'},
      ],
    },
  },
  getters: {

  },
  mutations: {
    login(state, user) {
      state.user = user;
    },
    selectLanguage(state, language) {
      Vuetify.framework.lang.current = language; // Cambia lenguaje de vuetify
      state.selectedLanguage = language;
    },
    selectSchool(state, school) {
      state.selectedSchool = school;
    },
  },
  actions: {
    async login({ commit }, request) {
        try {
            const ran = Math.floor(Math.random() * (10 - 1)) + 1;
            const user = await axios({
              ...AXIOS_DEFAULT_CONFIG,
              // propiedades no comunes
              url: '/users/' + ran,
              method: 'get',
              data: request,
            });

            commit('login', user.data.data);
        } catch (errors) {
          console.log(errors)
        }
    },
  },
  plugins: [
    VuejsStorage({
      keys: ['selectedLanguage'],
      namespace: 'roma',
      driver: VuejsStorage.drivers.localStorage,
    })
  ],
});
